<template>
    <div class='container'>
        <div ref='stats'></div>
        <board></board>
         <div class='reset' @click='resetCamera'>重置摄影机</div>
        <div ref='canvas' id='WebGL-output'></div>
    </div>
</template>

<script>
import math from '../mixins/math.js';
import mixin from '../mixins/threeMixin.js';
import loadModel from '../mixins/loadModel.js';
import P from '../mixins/params.js';
import board from '../components/board.vue';

const { PI, cos, sin, floor } = Math;
const { THREE, TWEEN } = window;
const mainGroup = new THREE.Group();
mainGroup.name = 'mainGroup';
const roadGroup = new THREE.Group();
const tunnelGroup = new THREE.Group();
export default {
    mixins: [mixin, math, loadModel],
    components: { board },
    data() {
        return {
            cameraLookAt: { x: 0, y: 0, z: 0 },
        };
    },
    computed: {
        controlSwitch() {
            return this.$store.state.controlSwitch;
        },
    },
    methods: {
        initWebGL() {
            const { scene, camera, renderer } = this.initBasics(this.$refs.canvas, {
                position: { x: 3, y: 8, z: 227 }, antialias: true
            }, {
                clearColor: 0x1F243A, shadowEnabled: true
            });
            this.scene = scene;
            this.camera = camera;
            this.renderer = renderer;
            this.scene.add(mainGroup);
            mainGroup.add(tunnelGroup);
            tunnelGroup.add(roadGroup);

            // this.addAxes(50);
            // const stats = this.initStats(this.$refs.stats);

            const control = this.addControl();
            const clock = new THREE.Clock();

            this.addTunnel();
            this.addRoad();
            this.addLight();
            this.addModel();
            this.addSigns();

            const renderScene = () => {
                // stats.update();
                TWEEN.update();
                if (this.controlSwitch) {
                    const delta = clock.getDelta();
                    control.update(delta);
                }
                requestAnimationFrame(renderScene);
                renderer.render(scene, camera);
            };
            renderScene();
        },
        addRoad() {
            const geo = this.initGeometry('Box', P.road.width, P.road.length, P.road.height);
            const mat = this.initMaterial('MeshLambert', {
                color: 0x38393A,
            });
            const mesh = new THREE.Mesh(geo, mat);
            mesh.receiveShadow = true;
            mesh.rotation.x = - PI / 2;
            roadGroup.add(mesh);
            // 道路线
            const lineMat = this.initMaterial('MeshLambert', {
                color: 0xD3D3D3,
            });
            const solidLineGeom = this.initGeometry('Plane', P.line.width, P.road.length);
            const solidLine = new THREE.Mesh(solidLineGeom, lineMat);
            solidLine.rotation.x = - PI / 2;
            solidLine.position.set(-7.5, P.road.height / 2 + 0.01, 0);
            roadGroup.add(solidLine);
            const solidLineClone = solidLine.clone();
            solidLineClone.position.x = 7.5;
            roadGroup.add(solidLineClone);

            const yellowLineMat = this.initMaterial('MeshLambert', {
                color: 0xDAA520,
            });
            const yellowLine = new THREE.Mesh(solidLineGeom, yellowLineMat);
            yellowLine.rotation.x = - PI / 2;
            yellowLine.position.set(2.5, P.road.height / 2 + 0.01, 0);
            roadGroup.add(yellowLine);

            const dashedLineGeom = this.initGeometry('Plane', P.line.width, P.line.length);
            const dashedLine = new THREE.Mesh(dashedLineGeom, lineMat);
            dashedLine.rotation.x = - PI / 2;
            dashedLine.position.set(-2.5, P.road.height / 2 + 0.01, 0);
            const number = P.road.length / (P.line.length + P.line.distance);
            for (let i = 0; i < number - 1; i++) {
                const dashedLineClone = dashedLine.clone();
                dashedLineClone.position.z = P.road.length / 2 - P.line.length / 2 - i * (P.line.length + P.line.distance);
                roadGroup.add(dashedLineClone);
            }
        },
        addTunnel() {
            const shapeGeom = this.createShapeGeom(0.3, P.tunnel.length);
            const mat = this.initMaterial('MeshLambert', {
                color: 0xffffff,
                transparent: true,
                opacity: 0.2,
            });
            const mesh = new THREE.Mesh(shapeGeom, mat);
            mesh.castShadow = true;
            mesh.position.z = -P.tunnel.length / 2;
            tunnelGroup.add(mesh);
            // 隧道拱门
            const doorGeom = this.createShapeGeom(1, 0.08);
            const doorMat = this.initMaterial('MeshLambert', {
                color: 0xC0C0C0,
            });
            const door = new THREE.Mesh(doorGeom, doorMat);
            door.castShadow = true;
            const doorClone = door.clone();
            door.position.z = P.tunnel.length / 2 + 2;
            doorClone.position.z = - P.tunnel.length / 2 - 2;
            tunnelGroup.add(door);
            tunnelGroup.add(doorClone);
        },
        addModel() {
            // 风机
            function fanAnim(target) {
                const obj = { r: 0 };
                const rotationAnim = new TWEEN.Tween(obj)
                    .to({ r: PI * 2}, 1000)
                    .onUpdate((obj) => {
                        target.children[1].rotation.z = obj.r;
                        target.children[2].rotation.z = obj.r;
                    })
                    .repeat(Infinity);
                rotationAnim.start();
            }
            const fanPromise = new Promise((resolve) => {
                this.loadModel('fan-shell', (fanShell) => {
                    fanShell.position.set(-0.04, 0.15, 0);
                    this.loadModel('fan-leaf', (fanLeaf) => {
                        fanLeaf.position.set(-0.04, -0.02, -1.2);
                        const fanList = [];
                        const number = P.tunnel.length / P.md.fan - 1;
                        for (let i = 0; i < number * 2; i++) {
                            const fanGroup = new THREE.Group();
                            fanGroup.name = `风扇-${i+1}`;
                            const fanShellClone = fanShell.clone();
                            const fanLeafClone = fanLeaf.clone();
                            const fanLeafClone2 = fanLeaf.clone();
                            fanLeafClone2.position.set(0, 0, 1.2);
                            fanGroup.add(fanShellClone);
                            fanGroup.add(fanLeafClone);
                            fanGroup.add(fanLeafClone2);
                            fanGroup.scale.set(1.5, 1.5, 1.5);
                            fanGroup.position.set(i % 2 === 0 ? -2 : 2, 7.6, -P.tunnel.length / 2 + P.md.fan + floor(i / 2) * P.md.fan);
                            fanGroup.rotation.z = (PI / 12) * (i % 2 === 0 ? 1 : -1);
                            fanAnim(fanGroup);
                            fanList.push(fanGroup);
                            mainGroup.add(fanGroup);
                        }
                        resolve(fanList);
                    });
                }); 
            })
            // 信号灯
            const signalPromise = new Promise((resolve) => {
                this.loadModel('signalLight', (signalLight) => {
                    signalLight.rotation.y = PI;
                    signalLight.name = '交通信号灯';
                    const position = { x: 6.4, y: 3.5, z: P.tunnel.length / 2 + 8 };
                    const { x, y, z } = position;
                    signalLight.position.set(x, y, z);
                    mainGroup.add(signalLight);
                    resolve(signalLight);
                });
            });
            // 情报板
            const boardPromise =  new Promise((resolve) => {
                this.loadModel('board', (board) => {
                    board.name = '安全提示牌';
                    board.position.set(0, 7.5, P.tunnel.length / 2 + 2);
                    mainGroup.add(board);
                    resolve(board);
                });
            });
            // 照明
            const lightPromise = new Promise((resolve) => {
                this.loadModel('light', (light) => {
                    light.rotation.z = -PI / 4.5;
                    light.scale.set(2, 2, 2);
                    light.position.set(0, 9, 0);
                    const lightList = [];
                    const number = P.tunnel.length / P.md.light - 1;
                    for (let i = 0; i < number; i++) {
                        const lightClone = light.clone();
                        lightClone.name = `隧道照明-${i+1}`;
                        lightClone.position.z = -P.tunnel.length / 2 + P.md.light + i * P.md.light;
                        mainGroup.add(lightClone);
                        lightList.push(lightClone);
                    }
                    resolve(lightList);
                });
            });
            // 监控器
            const cameraPromise = new Promise((resolve) => {
                this.loadModel('camera', (camera) => {
                    camera.position.set(7, 5.2, 0);
                    const cameraList = [];
                    const number = P.tunnel.length / P.md.camera - 1;
                    for (let i = 0; i < number; i++) {
                        const cameraClone = camera.clone();
                        cameraClone.name = `监控-${i+1}`;
                        cameraClone.position.z = -P.tunnel.length / 2 + P.md.camera + i * P.md.camera;
                        mainGroup.add(cameraClone);
                        cameraList.push(cameraClone);
                    }
                    resolve(cameraList);
                });
            });
            // 消防栓
            const hydrantPromise = new Promise((resolve) => {
                this.loadModel('hydrant', (hydrant) => {
                    hydrant.rotation.y = PI;
                    hydrant.position.set(-8.5, 1.2, 0);
                    const hydrantList = [];
                    const number = P.tunnel.length / P.md.hydrant - 1;
                    for (let i = 0; i < number; i++) {
                        const hydrantClone = hydrant.clone();
                        hydrantClone.name = `消防栓-${i+1}`;
                        hydrantClone.position.z = -P.tunnel.length / 2 + P.md.hydrant + i * P.md.hydrant;
                        mainGroup.add(hydrantClone);
                        hydrantList.push(hydrantClone);
                    }
                    resolve(hydrantList);
                });
            });
            Promise.all([fanPromise, signalPromise, boardPromise, lightPromise, cameraPromise, hydrantPromise]).then((res) => {
                let eventList = [];
                res.forEach((item) => {
                    if (Array.isArray(item)) {
                        item.forEach((group) => {
                            const meshList = this.findChildrenMesh(group);
                            eventList = eventList.concat(meshList);
                        });
                    } else {
                        const meshList = this.findChildrenMesh(item);
                        eventList = eventList.concat(meshList);
                    }
                });
                this.linstenClick(eventList);
            });
        },
        addSigns() {
            function signAnim(target) {
                function handleUpdate(obj) {
                    target.material.forEach((item) => {
                        item.opacity = obj.o;
                    });
                }
                const obj = { o: 1 };
                const tweenOut = new TWEEN.Tween(obj)
                    .to({ o: 0 }, 1500)
                     .easing(TWEEN.Easing.Sinusoidal.InOut)
                    .onUpdate(handleUpdate);
                const tweenIn = new TWEEN.Tween(obj)
                    .to({ o: 1 }, 1500)
                     .easing(TWEEN.Easing.Sinusoidal.InOut)
                    .delay(1000)
                    .onUpdate(handleUpdate);
                tweenOut.chain(tweenIn);
                tweenIn.chain(tweenOut);
                tweenOut.start();
            }
            for (let i = 0; i < 7; i++) {
                const sign = this.createSigns(i);
                // sign.position.z = -26.3 + i * 8.8;
                sign.position.z = P.tunnel.length / 2 - 2 - i * 8.8;
                signAnim(sign);
                mainGroup.add(sign);
            }
        },
        addLight() {
            const ambientLight = this.initLight('Ambient', {color: 0xffffff});
            const target = this.initTarget(0, 0, 0);
            const directionalLight = this.initLight('Directional', {
                color: 0x919191,
                position: {x: 70, y: 30, z: 40},
                castShadow: true,
                shadow: {
                    camera: {
                        near: 2,
                        far: 200,
                        left: -100,
                        right: 100,
                        top: 25,
                        bottom: -20,
                    },
                    mapSize: {
                        width: 2048,
                        height: 2048,
                    },
                },
            });
            directionalLight.target = target;
            // const helper = new THREE.CameraHelper(directionalLight.shadow.camera);
            // this.scene.add(helper);
            mainGroup.add(ambientLight);
            // mainGroup.add(directionalLight);
        },
        createShapeGeom(thickness = 0.1, length = 0.1) {
            const options = {
                depth: length,
                bevelThickness: 1,
                bevelSize: 0,
                bevelSegments: 3,
                bevelEnabled: true,
                curveSegments: 12,
                steps: 1
            };
            let vertices = [];
            const arr = [];
            for (let i = 0; i <= 180; i += 3) {
                const x = P.tunnel.radius * cos(this.deg2rad(i));
                const y = P.tunnel.radius * sin(this.deg2rad(i));
                vertices.push(this.v2(x, y));
                const dx = (P.tunnel.radius - thickness) * cos(this.deg2rad(180 - i));
                const dy = (P.tunnel.radius - thickness) * sin(this.deg2rad(180 - i));
                arr.push(this.v2(dx, dy));
            }
            vertices = vertices.concat(arr);
            const shape = new THREE.Shape(vertices);
            const shapeGeom = new THREE.ExtrudeGeometry(shape, options);
            return shapeGeom;
        },
        observe(target) {
            this.$store.commit('setControl', false);
            const unitVecotr = this.countVector(this.camera.position, target.position, true);
            const length = this.distance(this.camera.position, target.position);
            const cameraLook = new TWEEN.Tween(this.cameraLookAt)
                .to(target.position, 1000)
                .onUpdate((o) => {
                    this.camera.lookAt(o.x, o.y, o.z);
                });
            cameraLook.start();
            const d = length - 7;
            const v = {
                x: unitVecotr.x * d,
                y: unitVecotr.y * d,
                z: unitVecotr.z * d,
            };
            const destination = {
                x: this.camera.position.x + v.x,
                y: this.camera.position.y + v.y,
                z: this.camera.position.z + v.z,
            };
            const cameraMove = new TWEEN.Tween(this.camera.position)
                .to(destination, 3000)
                .onUpdate((o) => {
                    this.camera.position.set(o.x, o.y, o.z);
                });
            cameraMove.start();
        },
        resetCamera() {
            this.$store.commit('setControl', true);
            this.camera.position.set(3, 8, 227);
            this.camera.rotation.set(0, 0, 0);
            this.camera.lookAt(0, 0, 0);
            console.log(this.camera);
            this.cameraLookAt = { x: 0, y: 0, z: 0 };
        },
        linstenClick(objList) {
            this.listenEvent('click', this.camera, objList, (targetList) => {
                if (!targetList.length) return;
                const target = targetList[0].object;
                const targetName = target.name;
                const topGroup = this.findTopGroup(target);
                this.$store.commit('setObjectName', topGroup.name);
                this.observe(topGroup);
            });
        },
        addAxes(len) { // 默认辅助坐标系
            const axes = this.initAxes(len);
            this.scene.add(axes);
        },
        addControl() { // 摄影机控制器
            const control = this.initControls('Trackball', this.camera, this.renderer.domElement, {
                rotateSpeed: 1.0,
                zoomSpeed: 1.0,
                panSpeed: 1.0,
            });
            return control;
        },
    },
    mounted() {
        this.initWebGL();
    },
}
</script>

<style lang='less' scoped>
 .reset {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1rem;
    height: .4rem;
    position: fixed;
    top: .1rem;
    left: 50%;
    transform: translateX(-50%);
    background: white;
    cursor: pointer;
    border-radius: 5px;
}
</style>