import 'three/examples/js/loaders/OBJLoader.js';
import 'three/examples/js/loaders/MTLLoader.js';

export default {
    methods: {
        loadModel(modelName, cb) {
            const mtlLoader = new THREE.MTLLoader();
            const objLoader = new THREE.OBJLoader();
            mtlLoader.load(`./model/${modelName}.mtl`, (mat) => {
                mat.preload();
                objLoader.setMaterials(mat);
                objLoader.load(`./model/${modelName}.obj`, (obj) => {
                    cb && cb(obj);
                });
            });
        },
        createSigns(index) { // 160 95
            const geom = this.initGeometry('Box', 3.6, 2.15, 0.1);
            const url = require(`@/assets/imgs/signs-${index+1}.jpeg`);
            const texture = this.loadTexture(url);
            const mat = this.initMaterial('MeshLambert', { color: 0xffffff, transparent: true });
            const picMat = new THREE.MeshLambertMaterial({ map: texture, transparent: true });
            const materialArr = [];
            for (let i = 0; i < 6; i++) {
                if (i === 5) {
                    materialArr.push(picMat);                
                } else {
                    materialArr.push(mat);
                }
            }
            const faceMat = new THREE.MeshFaceMaterial(materialArr);
            const sign = new THREE.Mesh(geom, faceMat);
            sign.scale.set(-1, -1, -1);
            sign.position.set(-6.5, 9.5, 0);
            return sign;
        },
    }
}